import { render, remove } from '../framework/render';
import { DEFAULT_POINT } from '../const';
import PointFormView from '../view/main/point-form';
export default class NewPointPresenter {
    #mainListContainer;
    #handleDataChange;
    #handleDestroy;
    #handleFormClose;
    #pointNewComponent = null;
    #models;
    #point;
    constructor({ mainListContainer, models: models, onDataChange, onDestroy, onFormClose, }) {
        this.#mainListContainer = mainListContainer;
        this.#models = models;
        this.#point = DEFAULT_POINT;
        this.#handleDataChange = onDataChange;
        this.#handleDestroy = onDestroy;
        this.#handleFormClose = onFormClose;
    }
    init() {
        this.#point.id = crypto.randomUUID();
        if (this.#pointNewComponent !== null) {
            return;
        }
        this.#pointNewComponent = new PointFormView({
            point: this.#point,
            models: this.#models,
            isNewPoint: true,
            onFormSubmit: this.#handleFormSubmit,
            onDeleteClick: this.#handleCancelClick,
            onFormClose: () => null,
        });
        render(this.#pointNewComponent, this.#mainListContainer, 'afterbegin');
        document.addEventListener('keydown', this.#escKeyDownHandler);
    }
    destroy() {
        if (this.#pointNewComponent === null) {
            return;
        }
        this.#handleDestroy();
        this.#handleFormClose();
        remove(this.#pointNewComponent);
        this.#pointNewComponent = null;
        document.removeEventListener('keydown', this.#escKeyDownHandler);
    }
    setSaving() {
        this.#pointNewComponent?.updateElement({
            isDisabled: true,
            isSaving: true,
        });
    }
    setAborting() {
        const resetFormState = () => {
            this.#pointNewComponent?.updateElement({
                isDisabled: false,
                isSaving: false,
                isDeleting: false,
            });
        };
        this.#pointNewComponent?.shake(resetFormState);
    }
    #handleFormSubmit = (newPoint) => {
        this.#handleDataChange(1 /* UserAction.ADD_POINT */, 0 /* UpdateType.MINOR */, newPoint);
        this.destroy();
    };
    #handleCancelClick = () => {
        this.destroy();
    };
    #escKeyDownHandler = (evt) => {
        if (evt.key === 'Escape') {
            evt.preventDefault();
            this.destroy();
        }
    };
}
