/* eslint-disable @typescript-eslint/no-explicit-any */
import PointView from '../view/main/point';
import { render, replace, remove } from '../framework/render';
import dayjs from 'dayjs';
import PointFormView from '../view/main/point-form';
import { isDatesEqual } from '../utils/time/filters-time';
export default class PointPresenter {
    #mainListContainer;
    #pointComponent = null;
    #pointEditComponent = null;
    #models = null;
    #point = null;
    #handleDataChange;
    #handleModeChange;
    #mode = 0 /* Mode.DEFAULT */;
    constructor({ mainListContainer, onDataChange, onModeChange, }) {
        this.#mainListContainer = mainListContainer;
        this.#handleDataChange = onDataChange;
        this.#handleModeChange = onModeChange;
    }
    init({ point, models }) {
        this.#models = models;
        this.#point = point;
        const prevPointComponent = this.#pointComponent;
        const prevPointEditComponent = this.#pointEditComponent;
        this.#pointComponent = new PointView({
            point: this.#point,
            models: this.#models,
            onEditClick: this.#handleEditClick,
            onFavoriteClick: this.#handleFavoriteClick,
        });
        this.#pointEditComponent = new PointFormView({
            point: this.#point,
            models: this.#models,
            isNewPoint: false,
            onFormSubmit: this.#handleFormSubmit,
            onDeleteClick: this.#handleDeleteClick,
            onFormClose: this.#handleFormClose,
        });
        if (prevPointComponent === null || prevPointEditComponent === null) {
            render(this.#pointComponent, this.#mainListContainer);
            return;
        }
        if (this.#mode === 0 /* Mode.DEFAULT */) {
            replace(this.#pointComponent, prevPointComponent);
        }
        if (this.#mode === 1 /* Mode.EDITING */) {
            replace(this.#pointComponent, prevPointEditComponent);
            this.#mode = 0 /* Mode.DEFAULT */;
        }
        remove(prevPointComponent);
        remove(prevPointEditComponent);
    }
    destroy() {
        remove(this.#pointComponent);
        remove(this.#pointEditComponent);
    }
    resetView() {
        if (this.#mode !== 0 /* Mode.DEFAULT */) {
            this.#onDeleteClick();
        }
    }
    setSaving() {
        if (this.#mode === 1 /* Mode.EDITING */) {
            this.#pointEditComponent?.updateElement({
                isDisabled: true,
                isSaving: true,
            });
        }
    }
    setDeleting() {
        if (this.#mode === 1 /* Mode.EDITING */) {
            this.#pointEditComponent?.updateElement({
                isDisabled: true,
                isDeleting: true,
            });
        }
    }
    setAborting() {
        if (this.#mode === 0 /* Mode.DEFAULT */) {
            this.#pointComponent?.shake();
            return;
        }
        const resetFormState = () => {
            this.#pointEditComponent?.updateElement({
                isDisabled: false,
                isSaving: false,
                isDeleting: false,
            });
        };
        this.#pointEditComponent?.shake(resetFormState);
    }
    #switchToEditMode() {
        if (!this.#pointComponent || !this.#pointEditComponent) {
            return;
        }
        replace(this.#pointEditComponent, this.#pointComponent);
        document.addEventListener('keydown', this.#escKeyDownHandler);
        this.#handleModeChange();
        this.#mode = 1 /* Mode.EDITING */;
    }
    #switchToViewMode() {
        if (!this.#pointComponent || !this.#pointEditComponent) {
            return;
        }
        if (this.#point) {
            this.#pointEditComponent.reset(this.#point);
        }
        replace(this.#pointComponent, this.#pointEditComponent);
        document.removeEventListener('keydown', this.#escKeyDownHandler);
        this.#mode = 0 /* Mode.DEFAULT */;
    }
    #escKeyDownHandler = (evt) => {
        if (evt.key === 'Escape') {
            evt.preventDefault();
            if (this.#pointEditComponent) {
                if (this.#point) {
                    this.#pointEditComponent.reset(this.#point);
                }
                else {
                    throw new Error('Point is null');
                }
                this.#pointEditComponent.reset(this.#point);
            }
            else {
                throw new Error('pointEditComponent is null');
            }
            this.#switchToViewMode();
        }
    };
    #handleFavoriteClick = () => {
        if (this.#point) {
            this.#handleDataChange(0 /* UserAction.UPDATE_POINT */, 0 /* UpdateType.MINOR */, { ...this.#point, isFavorite: !this.#point.isFavorite });
        }
    };
    #handleEditClick = () => this.#switchToEditMode();
    #onDeleteClick = () => this.#switchToViewMode();
    #handleFormClose = () => this.#switchToViewMode();
    #handleFormSubmit = (updatedPoint) => {
        const point = this.#point;
        if (point) {
            const isMinorUpdate = !isDatesEqual(dayjs(point.dateFrom), dayjs(updatedPoint.dateFrom)) ||
                !isDatesEqual(dayjs(point.dateTo), dayjs(updatedPoint.dateTo)) ||
                point.offers !== updatedPoint.offers;
            this.#handleDataChange(0 /* UserAction.UPDATE_POINT */, isMinorUpdate ? 0 /* UpdateType.MINOR */ : 1 /* UpdateType.PATCH */, updatedPoint);
        }
    };
    #handleDeleteClick = (point) => {
        this.#handleDataChange(2 /* UserAction.DELETE_POINT */, 0 /* UpdateType.MINOR */, point);
    };
}
