import { SORT_TYPES } from '../const';
import Observable from '../framework/observable';
import { daySort, priceSort, timeSort } from '../utils/sorting';
export default class SortingModel extends Observable {
    #type = SORT_TYPES[0];
    getSortedPoints = (points, sortType) => {
        if (sortType === 'event' || sortType === 'offers') {
            return points;
        }
        switch (sortType) {
            case 'day':
                return points.sort(daySort);
            case 'time':
                return points.sort(timeSort);
            case 'price':
                return points.sort(priceSort);
        }
    };
    get type() {
        return this.#type;
    }
    setSortType(sortType) {
        this.#type = sortType;
        this._notify(0 /* UpdateType.MINOR */, this.#type);
    }
}
