import dayjs from 'dayjs';
const now = dayjs();
const isFuturePoints = (point) => dayjs(point.dateFrom).isAfter(now);
const isPresentPoints = (point) => dayjs(point.dateFrom).isBefore(now) && dayjs(point.dateTo).isAfter(now);
const isPastPoints = (point) => dayjs(point.dateTo).isBefore(now);
const isDatesEqual = (dateA, dateB) => (dateA === null && dateB === null) || dayjs(dateA).isSame(dateB, 'day');
export { isFuturePoints, isPresentPoints, isPastPoints, isDatesEqual };
