import AbstractView from '../../framework/view/abstract-view';
const getTemplate = () => '<button class="trip-main__event-add-btn  btn  btn--big  btn--yellow" type="button">New event</button>';
export default class NewButtonView extends AbstractView {
    #handleClick;
    constructor({ onButtonClick: onButtonClick }) {
        super();
        this.#handleClick = onButtonClick;
        this.element.addEventListener('click', this.#onClick);
    }
    get template() {
        return getTemplate();
    }
    #onClick = (evt) => {
        evt.preventDefault();
        this.#handleClick();
    };
}
