import { capitalLetter, checkMatch } from '../../utils/utils';
import { POINTS_TYPES } from '../../const';
import he from 'he';
const getTemplate = (pointsType, type) => `<div class="event__type-item"><input id="event-type-${pointsType}-1" class="event__type-input  visually-hidden" type="radio" name="${"event-type" /* FormNames.Type */}" value="${pointsType}" ${checkMatch(type, pointsType, 'checked')}>
  <label class="event__type-label  event__type-label--${pointsType}" for="event-type-${pointsType}-1">${he.escape(capitalLetter(pointsType))}</label>
</div>`;
const createPointsTypesListTemplate = (type) => POINTS_TYPES.map((pointsType) => getTemplate(pointsType, type)).join('');
export { createPointsTypesListTemplate };
