import dayjs from 'dayjs';
const priceSort = (pointA, pointB) => pointB.basePrice - pointA.basePrice;
const getDuration = ({ dateFrom, dateTo }) => dayjs(dateTo).diff(dayjs(dateFrom));
const timeSort = (pointA, pointB) => getDuration(pointB) - getDuration(pointA);
const daySort = (pointA, pointB) => dayjs(pointA.dateFrom).diff(dayjs(pointB.dateFrom));
export { priceSort, timeSort, daySort };
